.webp .hero{
    background-image: url('../../../build/img/modelos.webp');
}
.no-webp .hero{
    background-image: url('../../../build/img/modelos.jpg');
}
.hero{   
    display: flex;
    flex-direction: column;    
    width: 100%;
    min-height: 100vh;
    overflow: hidden;    
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: center 5rem;
    position: relative;
    @include dosk{
        min-height: 45vh;
    }
    &::before{
        content: '';
        width: 100%;
        height: 100%;        
        top: 0;
        left: 0;
        position: absolute;
        background-image: linear-gradient(to top, #ffffff 0%, #ffffff70 80%);
    }
}
.hero-content{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    flex: 1;            
    .hero__left{
        flex:1;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        z-index: 1;      
        h1{
            font-size: 6rem;
            text-align: center;
            line-height: 0.8;
            margin-bottom: 2rem;
            color: $fontColor;            
            @include tablet{
                font-size: 10rem;
            }
            @include desktop{
                font-size: 12rem;                
            }           
            span {
                display: block;
                font-size: 4.5rem;
                font-weight: $light;
                @include tablet{
                    font-size: 7.5rem;
                }
                @include desktop {
                    font-size: 9rem;
                }
            }
        }
        p.slogan{
            font-weight: $light;
            text-align: center;
            font-size: 2rem;
            transform: rotate(-8deg);
            @include tablet{
                font-size: 3rem;
            }
            
        }         
    }
    .hero__right{ 
        flex: 1;
        height: 100%;
        width: 100%;
        display: flex;                           
        .button{
            margin: 0 auto;
        }
           
        
    }
}