.footer{
    display: flex;
    flex-direction: column;
    align-items: center;   
    p{
        line-height: 2;
        text-align: center;
        font-size: 1.5rem;
        padding: 2rem 0;
    }
}
.social{
    margin-top: 2rem;
    .social__content{
        display: grid;
        text-align: center;
        line-height: 1;
        @include tablet{
            @include grid(2, $separacion/4);
            place-items: center center;
        }
        .logo{
            margin-bottom: 2rem;
            span{
                display: inline;
            }
            @include tablet{
                margin: 0;
            }            
        }        
    }
}

.social-networks {
    display: flex;
    list-style: none;
    position: relative;
    margin: 0 auto;
    li{
        margin: 0 2rem;
        cursor: pointer;
        position: relative;
        a{
            text-decoration: none;
            & .fab{
                color: $fontColor;
                font-size: 4rem;
            }
        }
        &::before{
            position: absolute;
            top:0rem;
            left: 0;
            font-family: "Font Awesome 5 Brands";
            font-size: 4rem;
            height: 0;
            transition: 0.5s ease-in-out;
            color:$primario;
            overflow: hidden;
        }
        &:hover::before{
            height: 100%;
        }
        &:nth-child(1)::before{
            content:'\f082' ;
            color: #1876f2;
            border-bottom: .4rem solid #1876f2;
        }
        &:nth-child(2)::before{
            content:'\e055' ;
            border-bottom: .4rem solid #d6249f;
            background: #d6249f;
            background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
            background-clip: text;
            -webkit-text-fill-color: transparent;
        }
        &:nth-child(3)::before{
            content:'\f081';
            color: #1da1f2;
            border-bottom: .4rem solid #1da1f2;
        }
    }    

}