.bolsa__compras{
    ul{
        width: 100%;
        li{
            display: flex;
            width: 100%;
            flex-direction: column;
            align-items: center;
            padding: 1rem;
            @include telefono{
                flex-direction: row;
            }
            img{
                width: 15rem;
                padding: 1rem;
                @include tablet{
                    margin-right: 2rem;
                }
            }
            .item__info{
                @include telefono{
                    text-align: left;
                }
                h3{
                    font-size: 1.8rem;
                }
                p{
                    line-height: 1.2;
                }
                .precio{
                    font-weight: $semi;
                }
            }
            & > p{
                flex: 1;
                padding: 2rem 3rem;
                text-align: right;
            }
            .fas{
                font-size: 3rem;                
                transition: all .5s ease-in-out;
                &:hover{
                    color:$primario;
                    cursor: pointer;
                }
            }
        }
    }
    .botones{
        @include tablet {
            display: flex;
            .button{
                padding: 1rem 2rem;
                width: auto;
            }
        }
        
    }
}